.catalog {
  background-color: $color-black-perl;
}

.catalog .container {
  padding: 3.2vw 18vw 6.3vw 18vw;

  @include vp-1023 {
    padding: 7vw 7vw 8.2vw;
  }

  @include vp-767 {
    height: 1300px;
    padding-bottom: 0;
    margin-bottom: 0;
  }
}

.catalog h2 {
  margin: 0;
  margin-bottom: 50px;
  margin-left: -6vw;
  overflow: hidden;
  color: $color-default-white;
  font-family: $ff-roboto;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 50.63px;

  @include vp-1919 {
    margin-left: 0;
  }

  @include vp-1023 {
    font-size: 43px;
    line-height: 58.38px;
  }

  @include vp-767 {
    max-width: 270px;
    margin-bottom: 40px;
    font-size: 28px;
    line-height: 39.38px;
  }
}

.catalog__title {
  display: inline;

  @include vp-767 {
    display: none;
  }

  &--mobile {
    display: none;

    @include vp-767 {
      display: inline;
    }
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  justify-content: center;
  column-gap: 70px;
  padding-left: 0;
  list-style: none;

  @include vp-1023 {
    grid-template-columns: minmax(670px, 920px);
    row-gap: 35px;
  }

  @include vp-767 {
    grid-template-columns: minmax(270px, 717px);
  }
}

.catalog__item-link {
  min-height: 33.5vw;
  position: relative;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &::after {
    content: "";
    position: absolute;
    right: 1px;
    bottom: 0;
    width: 60px;
    height: 60px;
    background-image: url("../img/svg/arrow.svg");
  }

  @include vp-1023 {
    min-height: 310px;
  }

  @include vp-767 {
    min-height: 270px;
    margin-bottom: 30px;

    &::after {
      width: 40px;
      height: 40px;
      background-position: center;
    }
  }

  &:hover,
  &:focus,
  &:active {
    outline: 1px solid $color-deep-sky-blue;
    opacity: 0.7;
  }
}

.catalog__item-link h3 {
  max-width: 250px;
  margin: 0;
  padding: 41px 40px 0;
  color: $color-default-white;
  font-family: $ff-roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;

  @include vp-1023 {
    max-width: 280px;
    padding: 5vw 5.6vw 0;
    font-size: 24px;
    line-height: 38px;
  }

  @include vp-767 {
    max-width: 180px;
    padding: 26px 30px 0;
    font-size: 18px;
  }
}

.catalog__item-link--production {
  background-color: $color-deep-sky-blue;
  background-image: image-set(
    url("../img/catalog-card-1.webp") 1x,
    url("../img/catalog-card-1@2x.webp") 2x,
    url("../img/catalog-card-1.jpg") 1x,
    url("../img/catalog-card-1@2x.jpg") 2x
    );

  @include vp-1023 {
    background-image: image-set(
      url("../img/catalog-card-1-tablet.webp") 1x,
      url("../img/catalog-card-1-tablet@2x.webp") 2x,
      url("../img/catalog-card-1-tablet.jpg") 1x,
      url("../img/catalog-card-1-tablet@2x.jpg") 2x
    );
  }

  @include vp-767 {
    background-image: image-set(
      url("../img/catalog-card-1-mobile.webp") 1x,
      url("../img/catalog-card-1-mobile@2x.webp") 2x,
      url("../img/catalog-card-1-mobile.jpg") 1x,
      url("../img/catalog-card-1-mobile@2x.jpg") 2x
    );
  }
}

.catalog__item-link--mounting {
  background-color: $color-deep-sky-blue;
  background-image: image-set(
    url("../img/catalog-card-2.webp") 1x,
    url("../img/catalog-card-2@2x.webp") 2x,
    url("../img/catalog-card-2.jpg") 1x,
    url("../img/catalog-card-2@2x.jpg") 2x
  );

  @include vp-1023 {
    background-image: image-set(
      url("../img/catalog-card-2-tablet.webp") 1x,
      url("../img/catalog-card-2-tablet@2x.webp") 2x,
      url("../img/catalog-card-2-tablet.jpg") 1x,
      url("../img/catalog-card-2-tablet@2x.jpg") 2x
    );
  }

  @include vp-767 {
    background-image: image-set(
      url("../img/catalog-card-2-mobile.webp") 1x,
      url("../img/catalog-card-2-mobile@2x.webp") 2x,
      url("../img/catalog-card-2-mobile.jpg") 1x,
      url("../img/catalog-card-2-mobile@2x.jpg") 2x
    );
  }
}

.catalog__item-link--tools {
  background-color: $color-deep-sky-blue;
  background-image: image-set(
    url("../img/catalog-card-3.webp") 1x,
    url("../img/catalog-card-3@2x.webp") 2x,
    url("../img/catalog-card-3.jpg") 1x,
    url("../img/catalog-card-3@2x.jpg") 2x
  );

  @include vp-1023 {
    background-image: image-set(
      url("../img/catalog-card-3-tablet.webp") 1x,
      url("../img/catalog-card-3-tablet@2x.webp") 2x,
      url("../img/catalog-card-3-tablet.jpg") 1x,
      url("../img/catalog-card-3-tablet@2x.jpg") 2x
    );
  }

  @include vp-767 {
    background-image: image-set(
      url("../img/catalog-card-3-mobile.webp") 1x,
      url("../img/catalog-card-3-mobile@2x.webp") 2x,
      url("../img/catalog-card-3-mobile.jpg") 1x,
      url("../img/catalog-card-3-mobile@2x.jpg") 2x
    );
    margin-bottom: -390px;
  }
}
